//write here your sass
+font(sf-pro-display-regular, sf-pro-display-regular)
+font(sf-pro-display-bold, sf-pro-display-bold)
+font(sf-pro-display-semibold, sf-pro-display-semibold)
+font(sf-pro-text-regular, sf-pro-text-regular)
+font(sf-pro-text-medium, sf-pro-text-medium)
+font(sf-pro-text-bold, sf-pro-text-bold)
+font(sf-pro-text-semibold, sf-pro-text-semibold)
+font(sf-pro-text-heavy, sf-pro-text-heavy)
+font(sf-pro-text-medium, sf-pro-text-medium)

=SFProText
  font-family: 'sf-pro-text-regular', Helvetica, Arial, sans-serif
=SFProTextMedium
  font-family: 'sf-pro-text-medium', Helvetica, Arial, sans-serif
=SFProTextBold
  font-family: 'sf-pro-text-bold', Helvetica, Arial, sans-serif
=SFProTextSemibold
  font-family: 'sf-pro-text-semibold', Helvetica, Arial, sans-serif
=SFProDisplay
  font-family: 'SFProDisplay-regular', Helvetica, Arial, sans-serif
=SFProDisplayBold
  font-family: 'sf-pro-display-bold', Helvetica, Arial, sans-serif
=SFProDisplaySemibold
  font-family: 'sf-pro-display-semibold', Helvetica, Arial, sans-serif
=SFProDisplayHeavy
  font-family: 'sf-pro-display-heavy', Helvetica, Arial, sans-serif
=SFProDisplayMedium
  font-family: 'sf-pro-display-medium', Helvetica, Arial, sans-serif

html,
body
  min-height: 100%

body
  min-height: 100%
  color: $dark
  font-size: 13px
  line-height: 1.4
  +SFProText

button:focus
  outline: none
  cursor: pointer

.title
  color: #1c1c1f
  letter-spacing: -0.1px

.icon

  &__img
    width: 36px
    height: 36px

.path
  opacity: 0.5

.cost
  opacity: .5
  letter-spacing: -0.1px
  line-height: 1.23
  +SFProTextSemibold

.check

  &__text
    padding-left: 15px
    +SFProTextMedium

.payment
  padding: 15px 0
  display: flex
  flex-direction: row
  justify-content: space-between
  align-items: center

.memory
  padding: 0 30px

  &__el
    padding-top: 18px
    padding-bottom: 19px
    display: flex
    justify-content: space-between
    border-bottom: 1px solid $borderGrey

.reviews
  text-align: left

.tree
  display: flex
  flex-direction: row
  flex-wrap: nowrap
  height: 100%

.bgc-grey
  background-color: #FCFBFB

.text-red
  color: $red
  opacity: 1!important

.text-green
  color: $green
  opacity: 1!important

.text-orange
  color: $orange
  opacity: 1!important